@charset "utf-8";
kayamba {margin:0;padding:0;display:block;}
* {box-sizing:border-box;}
body {margin:0;}
img {border:0;}
.color-title {color:#ffb416;}
.phone-arrow-down, .phone-arrow-up {display:none;}
.tab-arrow-down {transform: rotate(270deg); display: inline-block; margin: 0 30px;}
a {text-decoration:none;color:white;font-weight:bolder;display:inline-block;padding:15px;}
header {background-color:#242428;min-height:100vh;padding:0;margin:0;}
header button {background-color: transparent; color: white; border: 1px solid white; font-size: x-large; padding: 10px 35px; margin-top:50px;}

nav {margin:auto; width:100%;background-color:#36486b}
nav > section{display:inline-block;}
nav > section:first-child {width:30%;}
nav > section:first-child h1 {text-align:center; color:white;}
nav > section:last-child {width:70%;text-align:right;}

header #header-body {color:white; text-align:center;}
header #header-body h1 {font-size:6vw;}
header #header-body p {font-size:3vw;}

main {margin:0;}
main section:first-child {text-align:center; min-height:100vh; color:#242428;background-image: linear-gradient(5deg, #e2e2e226, transparent);}
main section:first-child h1 {font-size:4vw; margin: 0; padding: 30px;}
main section:first-child h1 ~ p {font-size:2vw; margin:0 20%;}
main section:first-child section {display:flex;margin:auto;flex-wrap:wrap;}
main section:first-child section div {width:45%; margin:auto;}
main section:first-child section div:first-child p {font-size:5vw; text-align:right;padding-left:40px;}
main section:first-child section div:last-child p {font-size:10vw; text-align:left;margin-top:20%; font-weight: bold;}
main section:nth-child(2) {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    min-height: 100vh;
    color: #242428;
    background-image: url(img/1877.jpg);
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
}
main section:nth-child(2) h1 {
    font-size: 4vw;
    margin: 0;
    color: white;
    width: 100%;
    padding: 30px;
    text-shadow: 0px 0px 9px black;
}
main section:nth-child(2) div {
    height: 215px;
    padding: 15px;
    width: 26.55%;
    color: white;
    background-color: #242428d1;
    border: 1px solid white;
    border-radius: 10px;
    margin: 20px auto;
    overflow:hidden;
    box-shadow: 0 0 3px 0px black;

}
main section:nth-child(2) img {max-width: 60%;}

@media screen and (max-width:678px) {
    main section:nth-child(2) div {
        width: 45%;
    }
}
@media screen and (max-width:500px){
    .phone-arrow-up {transform: rotate(90deg); display: inline-block; margin: 0 30px;}
    .phone-arrow-down {transform: rotate(270deg); display: inline-block; margin: 0 30px;}
    header {height:60px;}

    nav {height:100px;overflow:scroll;}
    nav > section{display:block;}
    nav > section a {display:block;text-align:center;}
    nav > section:first-child {background-color:#242428;z-index:1;}
    nav > section:first-child h1 {margin:13px auto;}
    nav > section:first-child, nav > section:last-child {width:100%;height:50px;border:1px solid transparent;}
    nav > section:first-child {position:fixed;top:}
    nav > section:last-child {margin-top:50px;}

    header #header-body h1 {font-size:12vw;margin-top:20vh;}
    header #header-body p {font-size:6vw;margin-top:10vh;}

    main section {padding-top: 7vh;}
    main section:first-child h1 {font-size:7vw;}
    main section:first-child h1 ~ p {font-size: 4vw; margin: 0 10%;}
    main section:first-child section div {width:100%; margin:auto;}
    main section:first-child section div:first-child p {font-size: 12vw; text-align: inherit; padding-left: 0; margin: 7vh;}
    main section:first-child section div:last-child p {font-size: 18vw; font-weight: bolder; text-align: inherit; margin-top: 0;}

    main section:nth-child(2) {background-image: none; border-top: 3px solid #eeeeee;}
    main section:nth-child(2) h1 {
        font-size: 10vw;
        color: #242428;
        padding: 0 0 15px 0;
        text-shadow: 0 0 2px black;
    }
    main section:nth-child(2) div {
        width: 100%;
        background-color: #242428;
        border: none; 
        border-radius: 0;
        margin: 7.5px 15px;
        box-shadow: 0 0 7px -2px black;
    }
    main section:nth-child(2) img {max-width: 75%;}

}